@import "../../../variable.scss";
.FollowersCard {
  max-width: 100%;
  border-radius: 0.7rem;
  gap: 1rem;
  display: flex;
  flex-direction: column;
  font-size: 13px;
  .follower {
    display: flex;
    justify-content: space-between;
    align-items: center;
    > div {
      display: flex;
      gap: 10px;
    }
    span {
      max-width:130px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .followerImage {
      width: 3.2rem;
      height: 3.2rem;
      border-radius: 50%;
    }
    .name {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      > span:nth-of-type(1) {
        font-weight: bold;
      }
    }
    .fc-button {
      height: 2rem;
      padding-left: 20px;
      padding-right: 20px;
    }
    .UnfollowButton {
      color: $orange;
      border: 2px solid $orange;
      cursor: pointer;
      background: transparent;
    }
  }
}
