@import "../../../variable.scss";
.ChatBox-container {
  background: #fff;
  border-radius: 1rem;
  display: grid;
  grid-template-rows: 14vh 60vh 13vh;
}

.chat-header {
  padding: 1rem 1rem 0rem 1rem;
  display: flex;
  flex-direction: column;
  .follower {
    div {
      display: flex;
      .name {
        display: flex;
        flex-direction: column;
        padding: 5px;
        span:nth-of-type(1) {
          font-size: 1.1rem;
          font-size: 600;
        }
        span:nth-of-type(2) {
          font-size: 0.8rem;
        }
      }
    }
  }
}

.chat-body {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 1.5rem;
  overflow: scroll;
}

.message {
  //   background:$buttonBg;
  background-color: #0a80ff;
  color: white;
  padding: 0.7rem;
  border-radius: 1rem 1rem 1rem 0;
  max-width: 28rem;
  width: fit-content;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  span:nth-of-type(2) {
    color: whitesmoke;
  }
}

.message > span:nth-child(2) {
  font-size: 0.7rem;
  //   color: $inputColor;
  align-self: end;
}

.own {
  align-self: flex-end;
  border-radius: 1rem 1rem 0 1rem;
  //   background: linear-gradient(135deg, #dddfe0 0%, #c3cfe2 100%);
  background: #ebebeb;
  color: #151515;
  span:nth-of-type(2) {
    color: #252525;
  }
}

.chat-sender {
  background: white;
  display: flex;
  justify-content: space-between;
  height: 3.5rem;
  align-items: center;
  gap: 1rem;
  padding: 0.8rem;
  border-radius: 1rem;
  align-self: end;
}
.chat-sender > input {
  height: 70%;
  background-color: rgb(236, 236, 236);
  border-radius: 0.5rem;
  border: none;
  outline: none;
  flex: 1;
  font-size: 14px;
  padding: 0px 15px 0px 15px;
}
.chat-sender > div:nth-of-type(1) {
  background: rgb(233, 233, 233);
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  cursor: pointer;
}
.chat-sender > div {
  height: 70%;
  padding: 0px 15px 0px 15px;
}
