@import "../../../variable.scss";
.Auth {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  gap: 4rem;
  position: relative;
  .userNamediv{
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}

.Auth > div {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* left side */
.a-left {
  gap: 2rem;
}

.a-left > img {
  width: 4rem;
  height: 4rem;
}

.Webname > h1 {
  font-size: 3rem;
  background-color: red;
  background-image: $buttonBg;
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
}

.Webname > h6 {
  font-size: 0.85rem;
}

/* right side */

.authForm {
  background-color: $cardColor;
  border-radius: 1rem;
  padding: 1rem;
  p {
    color: rgba(255, 0, 0, 0.747);
    margin:0;
    font-size: 13px;
  }
}

.authForm > div {
  width: 100%;
  align-items: center;
  justify-content: center;
}

.forgetpass{
  margin-top: 7px;
  font-size: 14px;
  color: $orange;
  text-align: right;
}
.a-right{
  display:flex;
  flex-direction:column;
  align-items: center;
  justify-content: center;
}
.googlebutton{
  height: 30px;
  color:$orange;
  border-radius: 4px;
  border: 1px solid $yellow ;
  width: 190px;
  
}

.separator{
  display:flex;
  align-items: center;
  .line{
    height: 1.5px;
    flex: 1;
    background-color: #cfccccd0;
  }
  h4{
    padding: 0 8px;
  }
}
