@import "../../../variable.scss";

.PostShare{
    display: flex;
    gap: 1rem;
    background-color: $cardColor;
    padding: 1rem;
    border-radius: 1rem;
    margin-bottom: 1rem;
    >img{
        border-radius: 50%;
        width: 3rem;
        height: 3rem;
    }
    >div{
        display: flex;
        flex-direction: column;
        width: 90%;
        gap: 1rem;
    }
    >div>input{
        background-color: $inputColor;
        border-radius: 10px;
        padding: 10px;
        font-size: 17px;
        border: none;
        outline: none;
    }
    .postOptions{
        display: flex;
        justify-content: space-around;
        .option{
            padding: 5px;
            padding-left: 10px;
            padding-right: 10px;
            border-radius: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 12px;
            :hover{
                cursor: pointer;
            }
        }
        .ps-button{
            padding: 5px;
            padding-left: 20px;
            padding-right: 20px;
            font-size: 12px
        }
    }
}

.previewImage{
    position: relative;
    >svg{
        position: absolute;
        right: 1rem;
        top: 0.5rem;
        cursor: pointer;
    }
    >img{
        width: 100%;
        max-height: 20rem;
        object-fit: cover;
        border-radius: 0.5rem;
    }
}
